	module.exports = {"data":{"markdownRemark":{"html":"<p>自社のWebサイト、どうすればこんなひどいHTMLになるんだろう……<code>&#x3C;noscript></code>が<code>&#x3C;title>～&#x3C;/title>～&#x3C;body></code>を跨いで括ってたりとか一体何考えてHTML組んでるんだか理解に悩む。入れ子って理解してるんか？更新日時をわざわざjavascriptで組んでたりとか頭大丈夫か？と思う。しかも更新日時表示だけの為に「JavscriptをOffにしていますとページが正常に(略」かよ。自社サーバなんだからSSIでも使えっての。タグが大文字小文字バラバラだし、属性の値を\"\"で括ってたり''で括ってたり、最悪括ってなかったりとかバラバラだし、そもそも スタイルシート読み込んでるんなら<code>&#x3C;font></code>タグとか<code>&#x3C;body></code>で<code>bgcolor=\"\"</code>とかやってんじゃねえよ。センタリングの為だけにテーブル使うなよ。スタイルシートって何のためにあるのか分かってるんか？重要な<code>img</code>リンクに<code>alt</code>属性がないとか、もう返す言葉も思い浮かばない。ちなみに<a href=\"http://www.htmllint.net/\">Another HTML-Lint</a>で採点したら見事マイナス1000点超え。 まがいなりにもWebアプリ系のソフト屋なら自社ページくらいもうちょっとマトモなHTML書こうぜ？そのうちしまいにゃ「この会社はHTMLすらマトモに書けない→スキルがない」ってなめられるぜ？ 自社ページ改修となるとどうせ稟議書→会議→偉い人の承認となるわけだが、まさか入社3ヶ月もしない新人がいきなり稟議書出すわけにもいかんだろうし、まぁこんなチラシの裏で愚痴ってみるテスト。 追記: 稟議書書いた。新人のクソガキの癖にと受理すらされなかった。ですよねー</p>","frontmatter":{"cover":null,"date":"2006-07-13T21:54:00+09:00","strDate":"2006/7/13","title":"仕事の愚痴"}}},"pathContext":{"path":"/ja/blog/2006/07/13-仕事の愚痴/","langKey":"ja"}}

